// Quotes custom
// -----------------------------------

// Quote primary
.quote-primary {
  .cite {
    @include header-h5;
    font-family: $font-family-sec;
  }

  .q {
    color: $gray-darker;
  }
}

.quote-primary__mark {
  position: relative;
  top: 6px;
  display: block;
  width: 37px;
  height: 27px;
  fill: $primary;
  @include flex-shrink(0);
}

.quote-primary__body {
  position: relative;
  padding: 25px 0;
  border-top: 1px solid #eaeced;

  &:before,
  &:after {
    content: '';
    position: absolute;
    bottom: 0;
    height: 13px;
    border-style: solid;
    border-color: #eaeced;
  }

  &:before {
    left: 0;
    width: 78px;
    border-width: 1px 1px 0 0;
  }

  &:after {
    right: 0;
    width: calc(100% - 90px);
    border-width: 1px 0 0 1px;
    transform-origin: 0 0;
    transform: skew(-45deg);
  }
}

.quote-primary__text {
  padding-top: 20px;
}

.quote-primary__footer {
  padding-left: 5px;
}

* + .quote-primary__footer {
  margin-top: 4px;
}

@media (min-width: $screen-xs-min) {
  .quote-primary__body {
    @include display-flex;
    @include flex-direction(row);
    @include flex-wrap(nowrap);
    padding: 30px 20px 42px 30px;
  }

  .quote-primary__text {
    padding-top: 0;
    padding-left: 16px;
  }

  .quote-primary__footer {
    padding-left: 30px;
  }
}

@media (min-width: $screen-md-min) {
  .quote-primary__body {
    padding-right: 40px;
  }
}

