//
// Variables Custom
// --------------------------------------------------


//== Colors
//
//## Gray and brand colors for use across Bootstrap.

$gray-base:               #000;
$gray-darker:             #151515;
$gray-dark:               #2b2c2f;
$gray:                    #555;
$gray-light:              #9b9b9b;
$gray-lighter:            #b7b7b7;

$primary-darker:          #443452;
$primary-dark:            #9a60be;
$primary:                 #aa6ecf;

$secondary-light:         #fdd482;
$secondary:               #dac390;
$secondary-dark:          #bda36c;

$primary-brown:           #515151;

$white:                   #fff;

$gray-1:                  #3e3e3e;
$gray-2:                  #f5f5f5;
$gray-3:                  #b8b8b6;
$gray-4:                  #d3d3d6;
$gray-5:                  #f7f8f9;
$gray-6:                  #e5e7e9;
$gray-7:                  #d4d9e0;
$gray-8:                  #e8e8e8;
$gray-9:                  #ccc;
$gray-10:                 #dbdbdb;
$gray-11:                 #ebebeb;
$gray-12:                 #343637;
$gray-13:                 #787878;

// Social
$facebook: #547abb;
$twitter: #44c0f3;
$google: #e75854;

//== Scaffolding
//
//## Settings for some of the most global styles.

//** Background color for `<body>`.
$body-bg:               #fff;
//** Global text color on `<body>`.
$text-color:            $gray-light;

//** Global textual link color.
$link-color:            $primary;
//** Link hover color set via `darken()` function.
$link-hover-color:      darken($link-color, 15%);
//** Link active
$link-press-color:      darken($link-color, 15%);
//** Link hover decoration.
$link-hover-decoration: none;

//== Typography
//
//## Font, line-height, and color for body text, headings, and more.

// Import fonts


$font-family-sans-serif:  "Lato", Helvetica, Arial, sans-serif;
$font-family-sans-serif-sec:  "Montserrat", Helvetica, Arial, sans-serif;
$font-family-serif:       Georgia, "Times New Roman", Times, serif;
//** Default monospace fonts for `<code>`, `<kbd>`, and `<pre>`.
$font-family-monospace:   Menlo, Monaco, Consolas, "Courier New", monospace;


$font-family-base:        $font-family-sans-serif;
$font-family-sec:         $font-family-sans-serif-sec;


// Template width (w/o units)
$template-width:          2050;

// Template width var for resizing
$template-width-min:      ($template-width+1)*1px;
$layout-boxed-width:      1920px;


// Base FZ & LH (w/o units)
$base-text-size:          14;
$base-lh-size:            24;

$base-vm-size:            (($base-text-size*100)/$template-width)*1vw;


$font-size-base:          $base-text-size * 1px;
$font-size-large:         ceil(($font-size-base * 1.25)); // ~18px
$font-size-small:         ceil(($font-size-base * 0.85)); // ~12px

$font-weight-base:        400;

//PSD FZ headers /FOR EDIT (w/o units)
$Size-h1:                 70;
$Size-h2:                 48;
$Size-h3:                 36;
$Size-h4:                 24;
$Size-h5:                 18;
$Size-h6:                 16;

//PSD LH headers /FOR EDIT (w/o units)
$Lh-h1:                   80;
$Lh-h2:                   58;
$Lh-h3:                   46;
$Lh-h4:                   28;
$Lh-h5:                   30;
$Lh-h6:                   20;


//Calculating FZ headers
$font-size-h1:            $Size-h1*1px;
$font-size-h2:            $Size-h2*1px;
$font-size-h3:            $Size-h3*1px;
$font-size-h4:            $Size-h4*1px;
$font-size-h5:            $Size-h5*1px;
$font-size-h6:            $Size-h6*1px;


//** line-height for headers /FOR EDIT
// $font-lh-h_: floor((PSD l-h / $font-size-h1));

$font-lh-h1:              ($Lh-h1 / $Size-h1);
$font-lh-h2:              ($Lh-h2 / $Size-h2);
$font-lh-h3:              ($Lh-h3 / $Size-h3);
$font-lh-h4:              ($Lh-h4 / $Size-h4);
$font-lh-h5:              ($Lh-h5 / $Size-h5);
$font-lh-h6:              ($Lh-h6 / $Size-h6);

//** Unit-less `line-height` for use in components like buttons.
$line-height-base:        ($base-lh-size / $base-text-size); // l-h/fz-base
//** Computed "line-height" (`font-size` * `line-height`) for use with `margin`, `padding`, etc.
$line-height-computed:    floor(($font-size-base * $line-height-base)); // ~20px

//** By default, this inherits from the `<body>`.
$headings-font-family:    $font-family-sec;
$headings-font-weight:    700;
$headings-line-height:    1.2;
$headings-color:          $gray-darker;

//== Forms variables
//

$input-height: 50px;
$input-color: $gray-light;
$input-color-placeholder: inherit;

$input-bg: $gray-2;
$input-border: $gray-lighter;
$input-border-radius: 0;

$input-font-size: 14px;
$input-line-height: 22px;
$input-font-weight: 400;

$error-color: #d9534f;
$success-color: #5cb85c;

$input-border-obj: none;
$input-padding-horizontal: 20px;
$input-line-height: round($input-font-size * 1.7);

$input-padding-vertical: round(($input-height - $input-line-height) / 2);
$input-padding: $input-padding-vertical $input-padding-horizontal;

@if ($input-border-obj != none) {
  $input-padding-vertical: round(($input-height - $input-line-height - (nth($input-border-obj, 1) * 2)) / 2);
  $input-padding: $input-padding-vertical $input-padding-horizontal;
}

$textarea-default-height:       135px;
$textarea-default-min-height:   $input-height;
$textarea-default-max-height:   round($textarea-default-height * 1.7);

//== Buttons variables
//
$btn-font-family:         $font-family-sec;
$btn-font-size:           13px;
$btn-font-weight:         700;
$btn-lh-size:             1.2;

//== Media queries breakpoints
//
//## Define the breakpoints at which your layout will change, adapting to different screen sizes.

// Extra small screen / phone
$screen-xs:                  480px;
$screen-xs-min:              $screen-xs;
$screen-phone:               $screen-xs-min;

// Small screen / tablet
$screen-sm:                  768px;
$screen-sm-min:              $screen-sm;
$screen-tablet:              $screen-sm-min;

// Medium screen / desktop
$screen-md:                  992px;
$screen-md-min:              $screen-md;
$screen-desktop:             $screen-md-min;

// Large screen / wide desktop
$screen-lg:                  1200px;
$screen-lg-min:              $screen-lg;
$screen-lg-desktop:          $screen-lg-min;

// Large screen / wide desktop
$screen-xlg:                  1600px;
$screen-xlg-min:              $screen-xlg;
$screen-xlg-desktop:          $screen-xlg-min;

// Extra Large screen / 4k desktop
$screen-xl:                  1800px;
$screen-xl-min:              $screen-xl;
$screen-xl-desktop:          $screen-xl-min;

// So media queries don't overlap when required, provide a maximum
$screen-xxs-max:             ($screen-xs-min - 1);
$screen-xs-max:              ($screen-sm-min - 1);
$screen-sm-max:              ($screen-md-min - 1);
$screen-md-max:              ($screen-lg-min - 1);
$screen-lg-max:              ($screen-xl-min - 1);

// Define media prefix
$medias: (0: null, $screen-xs-min : xs, $screen-sm-min : sm, $screen-md-min : md, $screen-lg-min : lg, $screen-xlg-min : xlg, $screen-xl-min : xl);


//== Flex Grid system
//
//## Define your custom flex responsive grid.

$shell-width:         300px;
$shell-xs-width:      480px;
$shell-sm-width:      750px;
$shell-md-width:      970px;
$shell-lg-width:      1200px;
$shell-xl-width:      1800px;

$range-ws:            50px;
$range-ws-sm:         30px;
$range-ws-lg:         66px;

$cell-count:          12;
$cell-spacing:        30px;
$cell-spacing-narrow: 10px;

//== Spacing
//
//##
$spacing-sm: .02em; //

//== Shadows
//
//##

$shadow-area-ambient: 0px 0px 1px 0 rgba(0, 0, 0, 0.15);
$shadow-area-xxs: 1px 1px 1px rgba(0, 0, 0, 0.15);
$shadow-area-xs: 0 1px 4px 0 rgba(0, 0, 0, 0.15);
$shadow-area-sm: 0 1px 10px 0 rgba(0, 0, 0, 0.15);
$shadow-area-md: 0 3px 11px 0 rgba(0, 0, 0, 0.15);
$shadow-area-lg: 0 5px 23px 0 rgba(0, 0, 0, 0.15);
$shadow-area-xl: 0 12px 36px 0 rgba(0, 0, 0,.15);