// Element-groups
@mixin group($offset-x, $offset-y) {
  transform: translateY(-$offset-y);
  margin-bottom: -$offset-y;
  margin-left: -$offset-x;

  > *, > *:first-child {
    display: inline-block;
    margin-top: $offset-y;
    margin-left: $offset-x;
  }

  > [class*="button-icon"]:first-child {
    display: inline-flex;
  }
}